// Dashboard.js
import React, { useState, useEffect } from "react";
import { Container, Paper, Typography, Button, Grid, Card,
   } from "@mui/material";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";
import { Link as RouterLink } from "react-router-dom";



const EnergyConsumptionChart = ({ chartData }) => (
  <Paper style={{ padding: "20px", marginBottom: "20px" }}>
    <Typography variant="h6" gutterBottom>
      Energy Consumption Chart
    </Typography>
    <BarChart
      width={500}
      height={300}
      data={chartData}
      margin={{ top: 5, right: 30, left: 20, bottom: 5 }}
    >
      <XAxis dataKey="month" />
      <YAxis />
      <CartesianGrid strokeDasharray="3 3" />
      <Tooltip />
      <Legend />
      <Bar dataKey="consumption" fill="#8884d8" />
    </BarChart>
  </Paper>
);

const ServiceLocationCard = ({ location }) => {
    return (
      <Paper
        component={RouterLink}
        to={`/service-location/${location.id}`}
        style={{
          padding: "10px",
          textDecoration: "none",
          color: "inherit",
          marginBottom: "20px",
          width: "30%",
          margin:"10px",
          position: "relative", // Ensure relative positioning for the absolute button
        }}
      >
        <Typography variant="subtitle1">{location.address}</Typography>
        <Typography variant="body2">{`${location.bedrooms} bedrooms, ${location.occupants} occupants`}</Typography>
        <Typography variant="body2">{`${location.squarefoot} sqft`}</Typography>
        <Typography variant="body2">{`${location.city}, ${location.state} ${location.zipcode}`}</Typography>
    </Paper>
    );
  };
    
  
    
  const ServiceLocations = ({
    locations,
    showAllLocations,
    setShowAllLocations,
  }) => (
    <Paper style={{ padding: "20px", marginBottom: "20px" }}>
      <Typography variant="h6" gutterBottom>
        Service Locations
      </Typography>
      <Grid container spacing={2}>
        {locations
          .slice(0, showAllLocations ? locations.length : 3)
          .map((location) => (
            <ServiceLocationCard key={location.id} location={location} />
          ))}
      </Grid>
      {!showAllLocations && (
        <Button
          variant="outlined"
          color="primary"
          onClick={() => setShowAllLocations(true)}
        >
          View More
        </Button>
      )}
    </Paper>
  );

const Dashboard = () => {
  const [chartData, setChartData] = useState([]);
  const [serviceLocations, setServiceLocations] = useState([]);
  const [showAllLocations, setShowAllLocations] = useState(false);

  useEffect(() => {
    const fetchChartData = async (accessToken) => {
      // Fetch chart data here
      // Example URL: http://localhost:8000/chart_data
    //   const chartResponse = await fetch("http://localhost:8000/chart_data", {
    //     headers: {
    //       Authorization: `Bearer ${accessToken}`,
    //     },
    //   });
    //   const chartData = await chartResponse.json();
    //   setChartData(chartData);
    };

    const fetchServiceLocations = async (accessToken) => {
      // Fetch service locations here
      const serviceLocsResponse = await fetch(
        "http://localhost:8000/service_locations",
        {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        }
      );
      const serviceLocsData = await serviceLocsResponse.json();
      setServiceLocations(serviceLocsData);
    };

    const accessToken = localStorage.getItem("accessToken");
    if (accessToken) {
      fetchChartData(accessToken);
      fetchServiceLocations(accessToken);
    }
  }, []);

  return (
    <Container>
      <Typography variant="h4" style={{ margin: "20px 0" }}>
        Dashboard
      </Typography>

      <EnergyConsumptionChart chartData={chartData} />
      <ServiceLocations
        locations={serviceLocations}
        showAllLocations={showAllLocations}
        setShowAllLocations={setShowAllLocations}
      />
    </Container>
  );
};

export default Dashboard;
